.footer{
    width: 100%;
    background: $bg;
    clip-path: polygon(50% 10%, 100% 0, 100% 100%, 0 100%, 0 0);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 130px;
}
.form{
    width: 55%;
    padding: 50px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.form__item{
    width: 100%;
    background: $form_bg;
    border-radius: 5px;
    padding: 15px 25px;
    border: none;
    margin-bottom: 20px;
    color: $text_color;
    font-family: $text;
    font-size: 24px;
}
.form__item:focus{
    outline: 1px solid $form_text;
}
::placeholder{
    color: $form_text;
    font-family: $text;
    font-size: 24px;
}
.form__item_big{
    resize: none;
    height: 190px;
    margin-bottom: 40px;
}
.footer__button{
    height: 64px;
    width: 30%;
}
.social-media__item{
    height: 64px;
    margin-right: 10px;
}
@media all and (max-width: 1200px){
    .form{
        width: 70%;
    }
    .footer__button{
        width: 40%;
    }
}
@media all and (max-width: 800px){
    .form{
        width: 80%;
    }
}
@media all and (max-width: 600px){
    .form{
        width: 90%;
    }
    .social-media{
        order: 1;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .social-media__item{
        margin: 0;
        height: 70px;
    }
    .footer__button{
        width: 100%;
        margin-bottom: 40px;
        font-size: 20px !important;
        height: 55px;
    }
    .form__item{
        margin-bottom: 10px;
    }
}