.nav{
    width: 100%;
    height: 50px;
    background: $nav_bg;
    display: flex;
    justify-content: center;
	transition: all 1s ease;
    position: sticky;
    top: 0px;
    z-index: 1;
    box-shadow: 0 3px 5px #3d5a8042;
}
// .nav_fixed{
//     position: fixed;
//     top: 0;
//     left: 0;
//     animation-name: fixing;
//     animation-duration: 1s;
//     animation-timing-function: ease;
//     animation-iteration-count: 1;
// }
.nav__list{
    width: 90%;
    display: flex;
    height: 50px;
    align-items:center;
}
.nav__item{
    height: 35px;
    margin-right: 10px;
    padding: 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    position: relative;
}
.nav__anchor{
    text-decoration: none;
    text-transform: uppercase;
    font-family: $text;
    color: $text_color;
    font-weight: bold;
    transition: all 0.5s ease;
}
.nav__item:hover .nav__anchor{
    color: $coral;
}

.nav__item_active .nav__anchor{
    color: $coral;
}

.nav__toggler{
    font-size: 30px;
    display: none;
}

@media all and (max-width:800px){
    .nav{
        justify-content: flex-end;
        align-items: center;
    }
    .nav__toggler{
        display: block;
        margin-right: 3%;
        color: $text_color;
        // margin-top: 0;
    }
    .nav__list{
        position: absolute;
        top: 50px;
        right: 0;
        height: calc( 100vh - 50px );
        padding: 20px 0;
        width: 0%;
        flex-direction: column;
        justify-content: space-around;
        background-color: $bg;
        backdrop-filter: blur(5px);
        background-blend-mode: 5px;
        transform: translateX(100%);
        transition: .5s;
        z-index: 999;
        overflow-y: scroll;
    }
    .nav_active{
        transform: translateX(0%);
        overflow-x: hidden !important;
        width: 100%;
    }
}