.header{
  width: 100%;
  height: 100vh;
  background: $bg;
  color: $text_color;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
}
.header__wrapper{
  display: flex;
  flex-wrap: wrap;
  align-content: center;
}
.header__headline{
  width: 100%;
  font-family: $headline;
  font-size: 114px;
}
.header__text{
  width: 100%;
  font-family: $text;
  padding: 30px 0;
}
.text_coral{
  color: $coral;
  text-shadow: -7px 0 0 $aqua;
}
.button-cont{
  width: 60%;
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.header__button{
  width: 40%;
  height: 70px;
}

@media all and (max-width: 1200px){
  .button-cont{
    width: 90%;
  }
}

@media all and (max-width: 1000px){
  .button-cont{
    width: 100%;
    margin-top: 75px;
  }
  .header__headline{
    font-size: 82px;
  }
}

@media all and (max-width: 800px){
  .header__button{
    height: 55px;
  }
  .header__headline{
    font-size: 72px;
  }
}

@media all and (max-width: 600px){
  .header__button{
    // height: 40px;
    width: 45%;
  }
  // .header__headline{
  //   font-size: 56px;
  // }
  // .button-cont{
  //   margin-top: 100px;
  // }
}

@media all and (max-width: 510px){
  .header__headline{
    font-size: 64px;
  }
  .header__button{
    width: 60%;
    margin-top: 30px;
  }
  .button-cont{
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
  }
  .text_coral{
    text-shadow: -4px 0 0 $aqua;
  }
}

@media all and (max-width: 450px){
  .header__headline{
    font-size: 56px;
  }
  .header__button{
    width: 80%;
  }
}

@media all and (max-width: 400px){
  .header__headline{
    font-size: 46px;
  }
}

@media all and (max-height: 800px) and (min-width: 900px){
  .header{
    height: 800px;
  }
}

@media all and (max-height: 600px) and (max-width: 900px){
  .header{
    height: 600px;
  }
}