
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


$bg: #BAD4FF;
$main_background: #FFF8F2;
$nav_bg: white;
$coral: #EE6C4D;
$aqua: #3D5A80;
$text_color: #293241;
$headline: 'Yeseva';
$text: 'Comfortaa';
$form_bg: rgba(255, 242, 243, 0.2);
$form_text: rgba(41, 50, 65, 0.85);

@font-face {
  font-family: "Comfortaa";
  src: url(fonts/Comfortaa-VariableFont_wght.ttf);
}
@font-face {
  font-family: "Yeseva";
  src: url(fonts/YesevaOne-Regular.ttf);
}

@import 'Header';
@import 'Nav';
@import 'Section';
@import 'Footer';
@import 'slider';

html{
  scroll-behavior: smooth;
}
#root{
  background: $main_background;
}
.wrapper{
  padding: 0 5%;
}

.text{
  font-family: $text;
  color: $text_color;
  font-size: 24px;
  line-height: 1.5;
}

.headline{
  font-family: $headline;
  text-align: center;
  color: $text_color;
  font-size: 48px;
  text-transform: uppercase;
  padding: 20px 55px;
  position: relative;
}
.headline:before{
  content: '';
  background: $text_color;
  width: 100%;
  position: absolute;
  height: 5px;
  border-radius: 5px;
  left: 0;
  bottom: 0px;
}
.button{
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border: 3px solid $text_color;
  background: none;
	transition: all 0.35s;
  box-sizing: border-box;
  box-shadow: 10px 10px 0 $coral;
  font-family: $text;
  font-weight: 600;
  font-size: 22px;
  color: $text_color;
  &:hover, &:active{
    box-shadow: (-10px) (-10px) 0 $text-color;
    background-color: $coral;
    border-color: $coral;
  }
}

.button__text{
  text-decoration: none;
  z-index: 2;
  font-family: $text;
  font-weight: 600;
  font-size: 22px;
  color: $text_color;
  padding: 15px 20%;
  cursor: pointer;
}

::selection{
  background: $aqua;
  color: $bg;
}
@media all and (max-width: 1000px){
  .text{
    font-size: 20px;
  }
}
@media all and (max-width: 800px){
  .button__text{
    font-size: 18px;
  }
  .button{
    border: 2px solid $text_color;
  }
  .text, ::placeholder{
    font-size: 18px;
  }
}

@media all and (max-width: 600px){
  // .button__text{
  //   font-size: 14px;
  // }
  .button{
    box-shadow: 5px 5px 0 $coral;
    &:hover{
      box-shadow: (-5px) (-5px) 0 $text-color;
    }
  }
  .text, ::placeholder{
    font-size: 16px;
  }
  .headline{
    font-size: 36px;
    padding: 17px 40px;
  }
}

@media all and (max-width: 450px){
  .button__text{
    font-size: 16px;
  }
  .text, ::placeholder{
    font-size: 14px;
  }
  .headline{
    font-size: 32px;
    padding: 15px 35px;
    &:before{
      height: 4px;
    }
  }
}