.swiper {
    width: 90%;
    height: 500px;
    margin: 40px 0;
    z-index: 0 !important;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    border-radius: 5px;
    background-size: 100%;
    background-position: center;
  
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .prev, .next{
    width: 50px;
    height: 50px;
    background: url(img/icons/arrow.svg);
    fill: $bg;
    background-size: contain;
    position: absolute;
    bottom: calc((100% - 100px)/2);
    // z-index: 1;
  }
  .prev{
    left: 0;
  }
  .next{
    right: 0;
    transform: rotate(180deg);
  }
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
.swiper__blur{
  width: 100%;
  height: 100%;
  border-radius: 5px;
  backdrop-filter: blur(5px);
  background: rgba(255,255,255,0.3);
  display: none;
}
.swiper__button{
  width: 80%;
}
.swiper-slide:hover{
  .swiper__blur{
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
  @media all and (max-width: 1400px){
    .swiper{
      height: 450px;
    }
  }
  @media all and (max-width: 1300px){
    .swiper{
      height: 400px;
    }
  }
  @media all and (max-width: 1100px){
    .swiper{
      height: 350px;
    }
  }
  @media all and (max-width: 1000px){
    .swiper{
      height: 450px;
      width: 85%;
    }
  }
  @media all and (max-width: 850px){
    .swiper{
      height: 400px;
      width: 85%;
    }
    .prev, .next{
      width: 40px;
      height: 40px;
    }
  }
  @media all and (max-width: 750px){
    .swiper{
      height: 350px;
    }
  }
  @media all and (max-width: 700px){
    .swiper{
      height: 500px;
      width: 80%;
    }
  }
  @media all and (max-width: 500px){
    .swiper{
      height: 400px;
    }
  }
  @media all and (max-width: 450px){
    .swiper{
      height: 320px;
      width: 75%;
    }
  }