.section{
    width: 100%;
    padding: 20px 5%;
    padding-top: 60px;
    background: $main_background;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.section__wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.section__content{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
}
.section__content_space-between{
    justify-content: space-between;
}
.section__text{
    width: 55%;
}
.section__img{
    width: 30%;
    border-radius: 5px;
}

@media all and (max-width: 1000px){
    .section__img{
        width:60%;
        order: -1;
        margin-bottom: 30px;
    }
    .section__text{
        width: 90%;
        text-align: center;
    }
    .section__content{
        justify-content: center;
    }
}

// diagram
.diagram{
    justify-content: center;
}
.diagram__line{
    width: 65%;
    height: 60px;
    background: $bg;
    margin: 15px;
    border-radius: 5px;
    display: flex;
}
.diagram__text{
    width: 20%;
    height: 100%;
    text-align: center;
    background: $aqua;
    color: $bg !important;
    border-radius: 5px 0 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 0px 4px rgba(0, 0, 0, 0.25);
}
.diagram__fill{
    height: 100%;
    background: $coral;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-radius: 0 5px 5px 0;
    padding-right: 10px;
    box-shadow: 5px 0px 4px rgba(0, 0, 0, 0.25);
}
.diagram__text::selection{
    background: $bg;
    color: $text-color;
}
@media all and (max-width: 1300px){
    .diagram__line{
        width: 70%;
    }
}


// works
.category{
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 55px;
}
.category__item{
    width: 25%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $text;
    text-transform: uppercase;
    font-size: 32px;
    color: $text_color;
    position: relative;
    cursor: pointer;
}
.category__item_active, .category__item:hover{
    color: $aqua;
}
.category__item:before{
    content: '';
    width: 0%;
    height: 3px;
    background: $coral;
    border-radius: 3px;
    position: absolute;
    bottom: 0;
    left: 5%;
    transition: all 1s ease;
}
.category__item:hover:before{
    width: 90%;
}
.category__item_active:before{
    // content: '';
    width: 90%;
