    // background: $coral;
    // border-radius: 3px;
    // position: absolute;
    // bottom: 0;
    // left: 5%;
}

// swiper
.swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
@media all and (max-width: 1200px){
    .section__img{
        width:40%;
    }
    .diagram__line{
        width: 80%;
    }
    .category{
        width: 90%;
    }
}
@media all and (max-width: 800px){
    .category{
        background: none;
        text-align: center;
        border: none;
        font-family: $text;
        text-transform: uppercase;
        font-size: 32px;
        color: $coral;
        width: auto;
    }
}
@media all and (max-width: 600px){
    .section__img{
        width: 90%;
    }
    .section__content{
        margin: 25px 0;
    }
    .diagram__line{
        width: 100%;
        margin: 10px;
        height: 50px;
    }
    .diagram__text{
        font-size: 10px !important;
    }
    .category{
        font-size: 20px;
    }
    .category__item{
        font-size: 20px;
    }
}